import React from 'react';

const TweetsList = ({ tweets }) => (
	<>
	<h3> Tweets:</h3>
	<h5> Query: 'social engineering attacks' </h5>
	<h6> Popular hashtags: #socialengineering #cybersecurity #informationsecurity
		#ransomware #phishing #smishing #vishing</h6>
	{tweets.map((tweet, key) => (
		<div className="tweet" key={key}>
			<h4>ID: {tweet.id}</h4>
			<p>Text: {tweet.text}</p>
		</div> 
	))}
	</>
)

export default TweetsList; 