import React from 'react';

const UpvotesSection = ({ termName, upvotes, setTermInfo }) => {

	const upvoteTerm = async () => {
		const result = await fetch(`/api/terms/${termName}/upvote`, {
			method: 'post',
		});
		const body = await result.json();
		setTermInfo(body);

	}
	return	(
		<div id="upvotes-section">
			<button onClick={() => upvoteTerm()}>Add Upvote</button>
			<p>This post has been upvoted {upvotes} times</p>	
		</div>

	);
}

export default UpvotesSection;