import React, { Component } from 'react';
import  {
  BrowserRouter as Router,
  Route,
  Switch,
} from 'react-router-dom';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import GitProfileSection from './components/GitProfileSection';
import TermsListPage from './pages/TermsListPage';
import TermPage from './pages/TermPage';
import References from './pages/References';
import TweetPage from './pages/TweetPage';
import NotFoundPage from './pages/NotFoundPage';
import NavBar from './NavBar';
import './App.css';


function App() {
  return (
    <Router>
      <div className="App">
        <NavBar />
          <div id="page-body">
            <Switch>
              <Route path="/" component={HomePage} exact/>
              <Route path="/about" component={AboutPage}/>
              <Route path="/git" component={GitProfileSection}/>
              <Route path="/references" component={References}/>
              <Route path="/terms-list" component={TermsListPage}/>
              <Route path="/term/:name" component={TermPage} />
              <Route path="/tweets" component={TweetPage}/>
              <Route component={NotFoundPage}/>
            </Switch> 
          </div>
      </div>
    </Router>
  );
}

export default App;
