import React from 'react';
import { Link } from 'react-router-dom';

const TermsList = ({ terms }) => (
	<>
	{terms.map((term, key) => (
		<Link className="term-list-item" key={key} to={`/term/${term.name}`}>
		<h3>{term.title}</h3>
		<p>{term.content[0].substring(0,100)}... </p>
		</Link>
	))}
	</> 

);

export default TermsList;