import React, { useState, useEffect }from 'react';
import TermsList from '../components/TermsList';
import UpvotesSection from '../components/UpvotesSection';
import CommentsList from '../components/CommentsList';
import AddCommentForm from '../components/AddCommentForm';
import termContent from './term-content';
import NotFoundPage from './NotFoundPage';

const TermPage = ({ match }) => {

	const name = match.params.name;
	const term = termContent.find(term => term.name === name);

	const [termInfo, setTermInfo] = useState({ upvotes: 0, comments: [] });

	useEffect(() => {
		const fetchData = async () => {
			const result = await fetch(`/api/terms/${name}`);
			const body = await result.json();
			setTermInfo(body);
		}
		fetchData();
		
	}, [name]);

	if (!term) return <NotFoundPage/>

	const otherTerms = termContent.filter(term => term.name !== name);	

	return(		
		<>
		<h1> {term.title}</h1>

		<UpvotesSection termName={name} upvotes={termInfo.upvotes}
		 setTermInfo={setTermInfo} />

		{term.content.map((paragraph, key) => (
			<p key={key}> {paragraph} </p> 
		))}
		<AddCommentForm termName={name} setTermInfo={setTermInfo} />
		<CommentsList comments={termInfo.comments} />
		<h3> Other Terms: </h3>
		<TermsList terms={otherTerms} />
		</> 
	); 
}

export default TermPage;